<template>
  <div class="container">
    <button @click="convert">Convert</button>

    <div class="marina">

      <textarea
        v-model="iostream_text"
        name="iostream"
        class="half"
      ></textarea>

      <textarea
        v-model="fstream_text"
        name="fstream"
        class="half"
      ></textarea>

    </div>
  </div>
</template>

<script>
export default {
  name: 'Marina',
  data: () => ({
    iostream_text: '',
    fstream_text: ''
  }),
  methods: {
    convert() {
      this.fstream_text = '';
      const lines = this.iostream_text
        .replace('include <iostream>', 'include <fstream>')
        .replace(
          /main\(\)\s*\n*{\s*\n*/,
          'main() {\n    ifstream input("input.txt");\n    ofstream output("output.txt");\n    '
        )
        .replace(/cin\s*>>/g, 'input >>')
        .replace(/getline\s*\(cin/g, 'getline(input')
        .replace(/cout\s*<</g, 'output <<')
        .split('\n');
      while (true) {
        const lastLine = lines[lines.length - 1];
        if (lastLine && !lastLine.includes('}')) {
          break;
        }
        lines.pop();
      }
      lines.push('    input.close();');
      lines.push('    output.close();');
      lines.push('    return 0;');
      lines.push('}');
      this.fstream_text = lines.join('\n');
    }
  }
};
</script>

